:local .root {
  display: flex;
  background: #f9f9f9;
  border-top: solid 1px rgba(0, 0, 0, 0.1);
  border-bottom: solid 1px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  height: 52px;
  position: relative;
  z-index: 2; }

:local .fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }

:local .tabBar {
  width: 90%;
  max-width: 800px;
  margin: 0 auto; }

:local .tabBarItem {
  padding: 15px 20px;
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  color: #666; }
  :local .tabBarItem:hover {
    color: #054a5a; }

:local .tabBarCount {
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.06);
  margin-left: 8px;
  font-size: 12px;
  padding: 2px 6px;
  display: inline; }

:local .active {
  color: #054a5a;
  box-shadow: inset 0 -3px 0 #054a5a;
  text-shadow: 0.35px 0 0, -0.35px 0 0; }

:local .tabBarResponvive {
  margin: 0 auto 0 0;
  width: 90%;
  align-items: center; }

:local .avatar {
  position: relative;
  margin: 0 20px;
  align-items: center;
  height: 32px; }
  :local .avatar img {
    display: inline-block;
    position: relative;
    width: 32px;
    height: 32px;
    overflow: hidden;
    border-radius: 50%; }
  :local .avatar .online {
    position: absolute;
    right: 0;
    bottom: 0; }

:local .tabBarList {
  display: block;
  top: 51px;
  width: 100%;
  border-radius: 3px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2);
  position: absolute; }

:local .tabBarItemResponsive {
  display: block;
  padding: 15px 12px 15px 32px;
  text-decoration: none;
  font-size: 16px;
  color: #666; }

:local .toggleButton {
  color: #666; }

:local .toggleButtonActive {
  color: #073648; }

:local .activeResponsive {
  background-color: rgba(8, 69, 93, 0.9);
  color: #fff;
  font-weight: normal; }
