:local .panel {
  max-width: 425px;
  margin-bottom: 15px;
  height: 140px;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #e5eaea;
  display: flex; }
  @media (max-width: 640px) {
    :local .panel {
      padding: 20px 15px; } }

:local .panelIcon {
  width: 21px;
  height: 21px;
  margin-right: 11px;
  color: #444; }

:local .panelContent {
  flex: 1; }

:local .contentHeader {
  color: #333;
  font-size: 18px;
  line-height: 1.33em; }

:local .contentDetail {
  height: 44px;
  line-height: 22px;
  color: #666;
  margin-bottom: 6px; }

:local .iconText {
  color: #666;
  font-size: 14px;
  display: inline-block; }

:local .controlViewMore {
  text-align: right;
  height: 100%;
  line-height: 50px;
  font-size: 14px;
  font-weight: bold;
  color: #1c8fc9;
  text-transform: uppercase;
  cursor: pointer; }

:local .statBadge {
  display: flex;
  font-size: 15px; }

:local .badgeCount {
  display: inline-block;
  padding: 0 15px 0 5px;
  color: #666;
  line-height: 24px; }
  @media (max-width: 640px) {
    :local .badgeCount {
      padding: 0 7px 0 3px; } }
