:local .endorsements {
  border-radius: 2px;
  background-color: rgba(254, 254, 254, 0.3);
  border: solid 1px #e5eaea;
  padding: 30px 30px 20px 30px;
  margin-bottom: 5px; }

:local .endorsement {
  display: flex;
  align-items: center;
  padding-bottom: 10px;
  flex-wrap: wrap; }

:local .endorserCount {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  background-color: #9eadb3;
  font-weight: bold;
  color: #f5f8f8;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3); }

:local .endorseCategory {
  font-size: 15px;
  line-height: 1.6;
  color: #444;
  margin-left: 15px; }

:local .dashLine {
  flex: 1;
  margin: 0 10px 0 15px;
  border-bottom: dashed 1px #aaa; }

@media (max-width: 640px) {
  :local .avatarList {
    -webkit-flex-basis: 100%;
    -ms-flex-preferred-size: 100%;
    flex-basis: 100%;
    padding-left: 45px; } }

:local .endorseAvatar {
  width: 22px;
  height: 22px;
  margin-left: 5px;
  display: inline-block;
  position: relative;
  overflow: hidden;
  font-size: 10px; }

:local .imageAvatar {
  object-fit: cover;
  position: absolute;
  border-radius: 50%;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%; }

:local .categoryPanel {
  border-radius: 2px;
  background-color: #fff;
  border: solid 1px #e5eaea;
  padding: 20px 30px;
  margin-bottom: 5px;
  color: #555;
  display: flex; }

:local .categoryIcon {
  margin-right: 15px;
  width: 30px; }
  :local .categoryIcon img {
    width: 30px;
    height: 30px; }

:local .contentHeader .headerName {
  display: inline-block;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.33;
  color: #333; }
  :local .contentHeader .headerName:hover {
    color: #333; }

:local .contentHeader .headerExperience {
  display: inline-block;
  font-size: 12px;
  line-height: 2;
  color: #999;
  margin-left: 10px; }

:local .contentDetail {
  padding: 8px 0 8px;
  line-height: 1.6;
  font-weight: normal;
  font-size: 15px;
  color: #555;
  word-break: break-word;
  white-space: pre-line; }

:local .contentTag {
  padding-top: 8px;
  line-height: 1.5;
  font-size: 12px; }
  :local .contentTag a {
    color: #7c9aa4;
    margin-right: 20px;
    display: inline-block; }
