:local .reviewStat {
  margin-bottom: 30px;
  display: flex; }

:local .averageBlock {
  text-align: center;
  font-size: 12px;
  line-height: 1.5;
  color: #666;
  border-radius: 6px;
  background-color: #fff;
  border: solid 1px #e5eaea;
  padding: 15px 35px; }
  @media (max-width: 768px) {
    :local .averageBlock {
      width: 170px;
      margin: 0 auto 20px auto; } }

:local .average {
  font-size: 50px;
  font-weight: bold;
  line-height: 1.2;
  color: #00b5e2;
  text-shadow: 0 1px 3px rgba(255, 255, 255, 0.5); }

:local .icon {
  color: #00b5e2;
  margin: 0 1px; }

:local .reviewRating {
  display: flex;
  font-size: 13px;
  line-height: 1.23;
  color: #333;
  align-items: center; }

:local .space {
  flex: 1;
  border-bottom: dashed 1px #ddd;
  margin: 0 15px; }

:local .ratingCount {
  line-height: 1.14;
  text-align: center;
  color: #666;
  font-size: 14px;
  width: 40px;
  text-align: center; }

:local .commentAvatar {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  overflow: hidden;
  font-size: 10px; }
  :local .commentAvatar img {
    object-fit: cover;
    position: absolute;
    border-radius: 50%;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; }

:local .commentDetail {
  flex: 1;
  border-radius: 2px;
  background-color: #fff;
  font-size: 15px;
  line-height: 1.6;
  color: #333;
  padding: 15px 30px;
  margin-left: 20px;
  position: relative; }
  :local .commentDetail .arrow {
    width: 0;
    height: 0;
    border-top: 14px solid #fff;
    border-left: 14px solid transparent;
    position: absolute;
    top: 0;
    left: -12px; }
  :local .commentDetail .content {
    padding-bottom: 6px; }
  :local .commentDetail div .name {
    display: inline-block;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.5;
    color: #333; }
  :local .commentDetail div .date {
    display: inline-block;
    font-size: 12px;
    line-height: 2;
    color: #999;
    margin-left: 10px; }

:local .viewMore {
  background-color: rgba(0, 0, 0, 0.05);
  color: #555;
  display: block;
  padding: 10px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  width: 100%;
  text-transform: uppercase; }
  :local .viewMore:hover {
    color: #555; }

:local .reviewDate {
  line-height: 1.71;
  letter-spacing: .5px;
  color: #666;
  padding: 10px 0 20px; }
