:local .header {
  display: flex;
  padding: 30px 0 0 0;
  align-items: center;
  width: 100%; }

:local .headerTitle {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.33;
  letter-spacing: .9px;
  color: #333;
  padding-bottom: 20px; }

:local .panel {
  border-radius: 2px;
  background-color: #fff;
  border: solid 1px #e5eaea; }

:local .panelState {
  display: flex;
  border-bottom: solid 1px #e5eaea;
  position: relative; }

:local .panelStateItem {
  border-right: solid 1px #e5eaea;
  text-align: center;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

@media (max-width: 768px) {
  :local .itemLesson {
    border-right: none; } }

:local .itemRate {
  border-right: none; }
  @media (max-width: 768px) {
    :local .itemRate {
      border-top: solid 1px #e5eaea; } }

:local .stateNumber {
  font-size: 20px;
  line-height: 1.75;
  color: #333; }

:local .stateName {
  font-size: 12px;
  line-height: 1.5;
  color: #999; }

:local .panelContent {
  padding: 20px;
  color: #555;
  font-size: 15px;
  line-height: 1.6; }

:local .panelInfo {
  padding: 0 30px 30px 30px;
  display: flex;
  align-items: baseline; }
  @media (max-width: 640px) {
    :local .panelInfo {
      display: block; } }

:local .panelInfoLeft {
  display: flex;
  flex: 1; }

:local .infoLeftItem {
  display: inline-block;
  color: #555;
  font-size: 13px;
  line-height: 1.54;
  margin-right: 30px; }

:local .socialLink {
  width: 30px;
  height: 30px;
  font-size: 16px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: solid 1px #e5eaea;
  color: #555;
  background: #fff;
  margin: 0 5px; }
  :local .socialLink:hover {
    color: #555; }
